import styled from 'styled-components';

const Container = styled.div`
  width: 100%;
  div {
    span {
      margin-right: 5px;
      &:not(:first-child) {
        background-color: #e0e0e0;
        border-radius: 25px;
        padding: 0 12px;
      }
    }
  }
`;

export { Container };
